.page {
  font-family: "Inter", sans-serif;
  font-size: 16px;
  line-height: 22px;
  color: #292929;

  scroll-behavior: smooth;

  &__body {
    margin: 0;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transform: translateX(100%);
    transition: transform $transitionTime;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin: 0 0 120px;

    &-title {
      margin-bottom: 48px;

      font-weight: 700;
      font-size: 32px;
      line-height: 32px;
      text-align: center;
      letter-spacing: -1.5px;

      @include onTablet {
        margin-bottom: 64px;

        font-size: 40px;
        line-height: 40px;
      }

      @include onDesktop {
        font-size: 48px;
        line-height: 48px;
      }

      &--last {
        margin-bottom: 48px;
      }
    }
  }
}
